.container {
  height: 100%;
  padding-top: 470px;
  padding-bottom: 30px;
}
.title {
  margin: 0px;
  padding: 50px;
  text-align: center;
  font-size: 36px;
  font-weight: 600;
}
.blogWrap {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
}
.blog {
  display: flex;
  flex-direction: column;
}
.blog3 {
  display: flex;
  flex-direction: column;
}

.img {
  width: 375px;
  height: 260px;
}
.textWrap {
  padding: 10px 20px;
  background-color: white;
}
.dark {
  background-color: #505053;
}
.light {
  background-color: white;
}
.text {
  max-width: 330px;
  font-size: 15px;
  line-height: 150%;
}
.titleContent {
  margin: 0px;
  font-size: 18px;
  line-height: 130%;
}
.contentButtonArrow {
  margin-left: 10px;
  margin-top: 1px;
  font-size: 25px;
  color: #5f6d92;
  float: right;
}
.linkWrap {
  display: flex;
}
.link {
  font-size: 12px;
  font-weight: bold;
  color: #5f6d92;
}

@media only screen and (max-width: 1024px) {
  .blogWrap {
    max-width: 930px;
  }
  .img {
    width: 300px;
    height: 200px;
  }
  .text {
    max-width: 280px;
  }
  .textWrap {
    padding: 5px 10px;
  }
}
@media only screen and (max-width: 768px) {
  .blogWrap {
    max-width: 700px;
  }
  .img {
    width: 340px;
    height: 220px;
  }
  .text {
    max-width: 300px;
  }
  .textWrap {
    padding: 1px 20px;
  }
  .blog3 {
    display: none;
  }
}
